export default {
	data() {
		return {
			cardCur: 0,
			swiperList: [{
				id: 0,
				type: 'image',
				url: '../../static/qr1.png'
			}, {
				id: 1,
				type: 'image',
				url: '../../static/qr2.png',
			}],
			dotStyle: true,
			towerStart: 0,
			direction: '',
			tis: "保存到相册",
			showBtn: false
		};
	},
	// 页面分享
	onShareAppMessage: function() {
		return {
			title: '微信付款',
			desc: '扫我付款',
			path: '/pages/myqr/main'
		}
	},
	onLoad() {



	},
	methods: {
		cardSwiper(e) {
			this.cardCur = e.detail.current
			console.log('当前',this.cardCur)
		},
		// 截图，调用webview、Bitmap方法
		printscreen() {
			this.tis = "正在保存"
			let ws = this.$mp.page.$getAppWebview();
			let bitmap = new plus.nativeObj.Bitmap();
			this.showBtn = false;
			this.$nextTick(function() {
				setTimeout(() => {
					ws.draw(bitmap, (e) => {
						this.showBtn = true;
						console.log('bitmap绘制图片成功');
						console.log("e: " + JSON.stringify(e));
						bitmap.save("_doc/Qr.jpg", {
							overwrite: true,
							quality: 100
						}, (i) => {
							plus.gallery.save(i.target, (e) => {
								uni.showToast({
									title: '保存成功'
								})
								this.tis = "保存到相册"
								bitmap.clear(); //销毁
							}, (e) => {
								bitmap.clear(); //销毁
							});
						}, (e) => {
							console.log('保存图片失败：' + JSON.stringify(e));
						});
					}, (e) => {
						console.log('bitmap绘制图片失败：' + JSON.stringify(e));
					});
				}, 200)
			})
		}
	}
}
